	module.exports = {"id":"addon-gallery","title":"Addon Gallery","body":"<p>This is a list of available addons for Storybook.</p>\n<h2>Built-In Addons.</h2>\n<p>These addons ship with Storybook by default. You can use them right away.</p>\n<h3><a href=\"https://github.com/storybooks/storybook/tree/master/packages/addon-actions\">Actions</a></h3>\n<p>With actions, you can inspect events related to your components. This is pretty neat when you are manually testing your components.</p>\n<p>Also, you can think of this as a way to document events in your components.</p>\n<h2>Third Party Addons</h2>\n<p>You need to install these addons directly from NPM in order to use them.</p>\n<h3><a href=\"https://github.com/philcockfield/storybook-host\">Host</a></h3>\n<p>A <a href=\"/docs/react-storybook/addons/introduction\">decorator</a> with\npowerful display options for hosting, sizing and framing your components.</p>\n<h3><a href=\"https://github.com/mthuret/storybook-addon-specifications\">Specs</a></h3>\n<p>This is a very special addon where it’ll allow you to write test specs directly inside your stories.\nYou can even run these tests inside a CI box.</p>\n<h3><a href=\"https://github.com/storybooks/storybook/tree/master/packages/addon-knobs\">Knobs</a></h3>\n<p>Knobs allow you to edit React props dynamically using the Storybook UI.\nYou can also use Knobs as dynamic variables inside your stories.</p>\n<h3><a href=\"https://github.com/storybooks/storybook/tree/master/packages/addon-notes\">Notes</a></h3>\n<p>With this addon, you can write notes for each story in your component. This is pretty useful when you are working with a team.</p>\n<h3><a href=\"https://github.com/storybooks/storybook/tree/master/packages/addon-info\">Info</a></h3>\n<p>If you are using Storybook as a style guide, then this addon will help you to build a nice-looking style guide with docs, automatic sample source code with a PropType explorer.</p>\n<h3><a href=\"https://github.com/storybooks/storybook/tree/master/packages/addon-options\">Options</a></h3>\n<p>The Storybook webapp UI can be customised with this addon. It can be used to change the header, show/hide various UI elements and to enable full-screen mode by default.</p>\n<h3><a href=\"https://github.com/yangshun/react-storybook-addon-chapters\">Chapters</a></h3>\n<p>With this addon, you can showcase multiple components (or varying component states) within a story by breaking it down into smaller categories (chapters) and subcategories (sections) for more organizational goodness.</p>\n<h3><a href=\"https://github.com/NewSpring/react-storybook-addon-backgrounds\">Backgrounds</a></h3>\n<p>With this addon, you can switch between background colors and background images for your preview components. It is really helpful for styleguides.</p>\n<h3><a href=\"https://github.com/sm-react/storybook-addon-material-ui\">Material-UI</a></h3>\n<p>Wraps your story into MuiThemeProvider. It allows you to add your custom themes, switch between them, make changes in the visual editor and download as JSON file</p>\n<h3><a href=\"https://github.com/tuchk4/storybook-readme\">README</a></h3>\n<p>With this addon, you can add docs in markdown format for each story. It very useful because most projects and components already have <a href=\"http://README.md\">README.md</a> files. Now it is easy to add them into your Storybook.</p>\n<h3><a href=\"https://github.com/joscha/storybook-addon-i18n-tools\">i18n tools</a></h3>\n<p>With this addon, you can test your storybooks with a different text-direction. It is very useful if you are working on components that have to work both in LTR as well as in RTL languages.</p>\n<h3><a href=\"https://github.com/evgenykochetkov/react-storybook-addon-props-combinations\">Props Combinations</a></h3>\n<p>Given possible values for each prop, renders your component with all combinations of prop values. Useful for finding edge cases or just seeing all component states at once.</p>\n<h3><a href=\"https://github.com/gvaldambrini/storybook-router\">StoryRouter</a></h3>\n<p>A <a href=\"/docs/react-storybook/addons/introduction\">decorator</a> that allows you to integrate react-router v.4 components in your stories.</p>\n"}

