	module.exports = {"id":"api","title":"API","body":"<h2>Core Addon API</h2>\n<p>This is the core addon API. This is how to get the addon API:</p>\n<pre><code class=\"language-js\"><span class=\"hljs-keyword\">import</span> addonAPI <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'@kadira/storybook-addons'</span>;\n</code></pre>\n<p>Have a look at the API methods for more details:</p>\n<h3>addonAPI.getChannel()</h3>\n<p>Get an instance to the channel where you can communicate with the manager and the preview. You can find this in both the addon register code and in your addon’s wrapper component (where used inside a story).</p>\n<p>It has a NodeJS <a href=\"https://nodejs.org/api/events.html\">EventEmitter</a> compatible API. So, you can use it to emit events and listen for events.</p>\n<h3>addonAPI.register()</h3>\n<p>This method allows you to register an addon and get the storybook API. You can do this only in the Manager App.\nSee how we can use this:</p>\n<pre><code class=\"language-js\"><span class=\"hljs-comment\">// Register the addon with a unique name.</span>\naddonAPI.register(<span class=\"hljs-string\">'kadira/notes'</span>, (storybookAPI) =&gt; {\n\n});\n</code></pre>\n<p>Now you’ll get an instance to our StorybookAPI. See the <a href=\"/docs/react-storybook/addons/api#storybook-api\">api docs</a> for Storybook API regarding using that.</p>\n<h3>addonAPI.addPanel()</h3>\n<p>This method allows you to add a panel to Storybook. (Storybook’s Action Logger is a panel). You can do this only in the Manager App.\nSee how you can use this method:</p>\n<pre><code class=\"language-js\"><span class=\"hljs-keyword\">const</span> MyPanel = <span class=\"hljs-function\"><span class=\"hljs-params\">()</span> =&gt;</span> (\n  <span class=\"xml\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span>&gt;</span>\n    This is a panel.\n  <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span></span>\n);\n\n<span class=\"hljs-comment\">// give a unique name for the panel</span>\naddonAPI.addPanel(<span class=\"hljs-string\">'kadira/notes/panel'</span>, {\n  <span class=\"hljs-attr\">title</span>: <span class=\"hljs-string\">'Notes'</span>,\n  <span class=\"hljs-attr\">render</span>: <span class=\"hljs-function\"><span class=\"hljs-params\">()</span> =&gt;</span> (\n    <span class=\"xml\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">MyPanel</span> /&gt;</span>\n  ),\n});\n</span></code></pre>\n<p>As you can see, you can set any React Component as the panel. Currently, it’s just a text. But you can do anything you want.</p>\n<p>You also pass the channel and the Storybook API into that. See:</p>\n<pre><code class=\"language-js\">addonAPI.register(<span class=\"hljs-string\">'kadira/notes'</span>, (storybookAPI) =&gt; {\n  <span class=\"hljs-comment\">// Also need to set a unique name to the panel.</span>\n  addonAPI.addPanel(<span class=\"hljs-string\">'kadira/notes/panel'</span>, {\n    <span class=\"hljs-attr\">title</span>: <span class=\"hljs-string\">'Notes'</span>,\n    <span class=\"hljs-attr\">render</span>: <span class=\"hljs-function\"><span class=\"hljs-params\">()</span> =&gt;</span> (\n      <span class=\"xml\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">Notes</span> <span class=\"hljs-attr\">channel</span>=<span class=\"hljs-string\">{addons.getChannel()}</span> <span class=\"hljs-attr\">api</span>=<span class=\"hljs-string\">{storybookAPI}/</span>&gt;</span>\n    ),\n  })\n})\n</span></code></pre>\n<h2>Storybook API</h2>\n<p>Storybook API allows you to access different functionalities of Storybook UI. You can move an instance to the Storybook API when you register an addon.</p>\n<p>Let’s have a look at API methods.</p>\n<h3>storybookAPI.selectStory()</h3>\n<p>With this method, you can select a story via an API. This method accepts two parameters.</p>\n<ol>\n<li>story kind name</li>\n<li>story name (optional)</li>\n</ol>\n<p>Let’s say you’ve got a story like this:</p>\n<pre><code class=\"language-js\">storiesOf(<span class=\"hljs-string\">'Button'</span>, <span class=\"hljs-built_in\">module</span>)\n  .add(<span class=\"hljs-string\">'with text'</span>, () =&gt; (\n    <span class=\"xml\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">Button</span> <span class=\"hljs-attr\">onClick</span>=<span class=\"hljs-string\">{action(</span>'<span class=\"hljs-attr\">clicked</span>')}&gt;</span>Hello Button<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">Button</span>&gt;</span></span>\n  ));\n</code></pre>\n<p>This is how you can select the above story:</p>\n<pre><code class=\"language-js\">storybookAPI.selectStory(<span class=\"hljs-string\">'Button'</span>, <span class=\"hljs-string\">'with text'</span>);\n</code></pre>\n<h3>storybookAPI.setQueryParams()</h3>\n<p>This method allows you to set query string parameters. You can use that as temporary storage for addons. Here’s how you set query params.</p>\n<pre><code class=\"language-js\">storybookAPI.setQueryParams({\n  <span class=\"hljs-attr\">abc</span>: <span class=\"hljs-string\">'this is abc'</span>,\n  <span class=\"hljs-attr\">bbc</span>: <span class=\"hljs-string\">'this is bbc'</span>,\n});\n</code></pre>\n<blockquote>\n<p>If you need to remove a query param, use <code>null</code> for that. For an example, let’s say we need to remove bbc query param. This is how we do it:</p>\n</blockquote>\n<pre><code class=\"language-js\">storybookAPI.setQueryParams({\n  <span class=\"hljs-attr\">bbc</span>: <span class=\"hljs-literal\">null</span>,\n});\n</code></pre>\n<h3>storybookAPI.getQueryParam()</h3>\n<p>This method allows you to get a query param set by above API <code>setQueryParams</code>. For example, let’s say we need to get the bbc query param. Then this how we do it:</p>\n<pre><code class=\"language-js\">storybookAPI.getQueryParam(<span class=\"hljs-string\">'bbc'</span>);\n</code></pre>\n<h3>storybookAPI.onStory(fn)</h3>\n<p>This method allows you to register a handler function which will be called whenever the user navigates between stories.</p>\n<pre><code class=\"language-js\">storybookAPI.onStory(<span class=\"hljs-function\">(<span class=\"hljs-params\">kind, story</span>) =&gt;</span> <span class=\"hljs-built_in\">console</span>.log(kind, story));\n</code></pre>\n"}

