	module.exports = {"id":"introduction","title":"Intro to Addons","body":"<p>By default, Storybook comes with a way to list stories and visualize them. Addons implement extra features for Storybooks to make them more useful.</p>\n<p>Basically, there are two types of addons. (Decorators and Native Addons)</p>\n<h2>1. Decorators</h2>\n<p>These are wrapper components or Storybook decorators that wrap a story.</p>\n<h3>Wrapper Components</h3>\n<p>For example, let’s say we want to center a story rendered on the screen. For that, we can use a wrapper component like this:</p>\n<pre><code class=\"language-js\"><span class=\"hljs-keyword\">const</span> Center = <span class=\"hljs-function\">(<span class=\"hljs-params\">{ children }</span>) =&gt;</span> (\n  <span class=\"xml\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span> <span class=\"hljs-attr\">style</span>=<span class=\"hljs-string\">{{</span> <span class=\"hljs-attr\">textAlign:</span> \"<span class=\"hljs-attr\">center</span>\" }}&gt;</span>\n    { children }\n  <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span></span>\n);\n</code></pre>\n<p>Then we can use it when writing stories.</p>\n<pre><code class=\"language-js\">storiesOf(<span class=\"hljs-string\">'Button'</span>, <span class=\"hljs-built_in\">module</span>)\n  .add(<span class=\"hljs-string\">'with text'</span>, () =&gt; (\n    <span class=\"xml\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">Center</span>&gt;</span>\n      <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">Button</span> <span class=\"hljs-attr\">onClick</span>=<span class=\"hljs-string\">{action(</span>'<span class=\"hljs-attr\">clicked</span>')}&gt;</span>Hello Button<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">Button</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">Center</span>&gt;</span></span>\n  ));\n</code></pre>\n<h3>Storybook Decorators</h3>\n<p>You can also expose this functionality as a Storybook decorator and use it like this.</p>\n<pre><code class=\"language-js\"><span class=\"hljs-keyword\">const</span> CenterDecorator = <span class=\"hljs-function\">(<span class=\"hljs-params\">story</span>) =&gt;</span> (\n  <span class=\"xml\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span> <span class=\"hljs-attr\">style</span>=<span class=\"hljs-string\">{{</span> <span class=\"hljs-attr\">textAlign:</span> \"<span class=\"hljs-attr\">center</span>\" }}&gt;</span>\n    {story()}\n  <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span></span>\n);\n\nstoriesOf(<span class=\"hljs-string\">'Button'</span>, <span class=\"hljs-built_in\">module</span>)\n  .addDecorator(CenterDecorator)\n  .add(<span class=\"hljs-string\">'with text'</span>, () =&gt; (\n    <span class=\"xml\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">Button</span> <span class=\"hljs-attr\">onClick</span>=<span class=\"hljs-string\">{action(</span>'<span class=\"hljs-attr\">clicked</span>')}&gt;</span>Hello Button<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">Button</span>&gt;</span></span>\n  ))\n  .add(<span class=\"hljs-string\">'with some emojies'</span>, () =&gt; (\n    <span class=\"xml\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">Button</span> <span class=\"hljs-attr\">onClick</span>=<span class=\"hljs-string\">{action(</span>'<span class=\"hljs-attr\">clicked</span>')}&gt;</span>😀 😎 👍 💯<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">Button</span>&gt;</span></span>\n  ));\n</code></pre>\n<p>You can also add a decorator globally for all stories like this:</p>\n<pre><code class=\"language-js\"><span class=\"hljs-keyword\">import</span> {\n    storiesOf, action, addDecorator\n} <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'@kadira/storybook'</span>;\n\n<span class=\"hljs-keyword\">const</span> CenterDecorator = <span class=\"hljs-function\">(<span class=\"hljs-params\">story</span>) =&gt;</span> (\n  <span class=\"xml\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span> <span class=\"hljs-attr\">style</span>=<span class=\"hljs-string\">{{</span> <span class=\"hljs-attr\">textAlign:</span> \"<span class=\"hljs-attr\">center</span>\" }}&gt;</span>\n    {story()}\n  <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span></span>\n);\naddDecorator(CenterDecorator);\n\nstoriesOf(<span class=\"hljs-string\">'Welcome'</span>, <span class=\"hljs-built_in\">module</span>)\n  .add(<span class=\"hljs-string\">'to Storybook'</span>, () =&gt; (\n    <span class=\"xml\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">Welcome</span> <span class=\"hljs-attr\">showApp</span>=<span class=\"hljs-string\">{linkTo(</span>'<span class=\"hljs-attr\">Button</span>')}/&gt;</span>\n  ));\n\nstoriesOf('Button', module)\n  .add('with text', () =&gt; (\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">Button</span> <span class=\"hljs-attr\">onClick</span>=<span class=\"hljs-string\">{action(</span>'<span class=\"hljs-attr\">clicked</span>')}&gt;</span>Hello Button<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">Button</span>&gt;</span>\n  ))\n  .add('with some emojies', () =&gt; (\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">Button</span> <span class=\"hljs-attr\">onClick</span>=<span class=\"hljs-string\">{action(</span>'<span class=\"hljs-attr\">clicked</span>')}&gt;</span>😀 😎 👍 💯<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">Button</span>&gt;</span>\n  ));\n</span></code></pre>\n<blockquote>\n<p>You can call <code>addDecorator()</code> inside the story definition file as shown above. But adding it to the Storybook config file is a much better option.</p>\n</blockquote>\n<h2>2. Native Addons</h2>\n<p>Native addons use Storybook as a platform and interact with it. Native addons can add extra features beyond wrapping stories.</p>\n<p>For example, <a href=\"https://github.com/storybooks/storybook/tree/master/packages/addon-actions\">storybook-actions</a> is such an addon.</p>\n<p><img src=\"../static/addon-actions-demo.gif\" alt=\"Demo of Storybook Addon Actions\"></p>\n<blockquote>\n<p>This addon ships with Storybook by default. <a href=\"https://github.com/storybooks/storybook/tree/master/packages/addon-actions\">Check here</a> for more info.</p>\n</blockquote>\n<p>It will allow you to inspect the parameters of any event of your components.</p>\n<p>See the following links to learn more about native addons:</p>\n<ul>\n<li><a href=\"/docs/react-storybook/addons/using-addons\">Using addons</a></li>\n<li><a href=\"/docs/react-storybook/addons/addon-gallery\">Addon gallery</a></li>\n<li><a href=\"/docs/react-storybook/addons/writing-addons\">Write your own addon</a></li>\n</ul>\n"}

