	module.exports = {"id":"using-addons","title":"Using Addons","body":"<p>By default, Storybook comes with two addons, which are <a href=\"https://github.com/storybooks/storybook/tree/master/packages/addon-actions\">actions</a> and <a href=\"https://github.com/storybooks/storybook/tree/master/packages/addon-links\">links</a>. But you can use any third party addons distributed via NPM.</p>\n<p>Here’s how to do it.</p>\n<p>Now we are going to use an addon called <a href=\"https://github.com/storybooks/storybook/tree/master/packages/addon-notes\">Notes</a>. Basically, it allows you to write notes for your stories.</p>\n<p>First, you need to create a file called <code>addons.js</code> inside the storybook config directory and add the following content:</p>\n<pre><code class=\"language-js\"><span class=\"hljs-keyword\">import</span> <span class=\"hljs-string\">'@kadira/storybook/addons'</span>;\n</code></pre>\n<p>This will load our default addons.</p>\n<p>Then install the notes addon with:</p>\n<pre><code class=\"language-sh\">npm i --save <span class=\"hljs-string\">'@kadira/storybook-addon-notes'</span>;\n</code></pre>\n<p>After that, add it to the addons.js like this:</p>\n<pre><code class=\"language-js\"><span class=\"hljs-keyword\">import</span> <span class=\"hljs-string\">'@kadira/storybook/addons'</span>;\n<span class=\"hljs-keyword\">import</span> <span class=\"hljs-string\">'@kadira/storybook-addon-notes/register'</span>;\n</code></pre>\n<p>Then you’ll be able to see those notes when you are viewing the story.</p>\n<p><img src=\"../static/stories-without-notes.png\" alt=\"Stories without notes\"></p>\n<p>Now when you are writing a story it like this and add some notes:</p>\n<pre><code class=\"language-js\"><span class=\"hljs-keyword\">import</span> React <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'react'</span>;\n<span class=\"hljs-keyword\">import</span> { storiesOf, action, linkTo } <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'@kadira/storybook'</span>;\n<span class=\"hljs-keyword\">import</span> Button <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'./Button'</span>;\n<span class=\"hljs-keyword\">import</span> { WithNotes } <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'@kadira/storybook-addon-notes'</span>;\n\nstoriesOf(<span class=\"hljs-string\">'Button'</span>, <span class=\"hljs-built_in\">module</span>)\n  .add(<span class=\"hljs-string\">'with some emoji'</span>, () =&gt; (\n    <span class=\"xml\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">WithNotes</span> <span class=\"hljs-attr\">notes</span>=<span class=\"hljs-string\">{</span>'<span class=\"hljs-attr\">Here</span> <span class=\"hljs-attr\">we</span> <span class=\"hljs-attr\">use</span> <span class=\"hljs-attr\">some</span> <span class=\"hljs-attr\">emoji</span> <span class=\"hljs-attr\">as</span> <span class=\"hljs-attr\">the</span> <span class=\"hljs-attr\">Button</span> <span class=\"hljs-attr\">text.</span> <span class=\"hljs-attr\">Isn</span>\\\\'<span class=\"hljs-attr\">t</span> <span class=\"hljs-attr\">it</span> <span class=\"hljs-attr\">look</span> <span class=\"hljs-attr\">nice</span>?'}&gt;</span>\n      <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">Button</span> <span class=\"hljs-attr\">onClick</span>=<span class=\"hljs-string\">{action(</span>'<span class=\"hljs-attr\">clicked</span>')}&gt;</span>😀 😎 👍 💯<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">Button</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">WithNotes</span>&gt;</span></span>\n  ));\n</code></pre>\n<p>Then you’ll be able to see those notes when you are viewing the story.</p>\n<p><img src=\"../static/stories-with-notes.png\" alt=\"Stories with notes\"></p>\n<p>Just like this, you can install any other addon and use it. Have a look at our <a href=\"/docs/react-storybook/addons/addon-gallery\">addon gallery</a> to discover more addons.</p>\n<blockquote>\n<p>This particular addon has created a panel in Storybook. Some addons may not create a panel and may use some other Storybook platform features.</p>\n<p>So, look at the addon’s own documentation on how to use it.</p>\n</blockquote>\n"}

