	module.exports = {"id":"writing-addons","title":"Writing Addons","body":"<p>This is a complete guide on how to create addons for Storybook.</p>\n<h2>Storybook Basics</h2>\n<p>Before we begin, we need to learn a bit about how Storybook works. Basically, Storybook has a <strong>Manager App</strong> and a <strong>Preview Area</strong>.</p>\n<p>Manager App is the client side UI for Storybook. Preview Area is the place where the story is rendered. Usually the Preview Area is an iframe.</p>\n<p>When you select a story from the Manager App, the relevant story is rendered inside the Preview Area.</p>\n<p><img src=\"../static/storybook-components.png\" alt=\"Storybook Components\"></p>\n<p>As shown in the above image, there’s a communication channel that the Manager App and Preview Area use to communicate with each other.</p>\n<h2>Capabilities</h2>\n<p>With an addon, you can add more functionality to Storybook. Here are a few things you could do:</p>\n<ul>\n<li>Add a panel to Storybook (like Action Logger).</li>\n<li>Interact with the story and the panel.</li>\n<li>Set and get URL query params.</li>\n<li>Select a story.</li>\n<li>Register keyboard shortcuts (coming soon).</li>\n</ul>\n<p>With this, you can write some pretty cool addons. Look at our <a href=\"/docs/react-storybook/addons/addon-gallery\">Addon gallery</a> to have a look at some sample addons.</p>\n<h2>Getting Started</h2>\n<p>Let’s write a simple addon for Storybook. It’s a Notes addon on which you can display some notes for a story.</p>\n<blockquote>\n<p>Just for the simplicity, we’ll write the addon right inside our app. But we can easily move it into a separate NPM module.</p>\n</blockquote>\n<h2>How it looks</h2>\n<p>We write a story for our addon like this:</p>\n<pre><code class=\"language-js\"><span class=\"hljs-keyword\">import</span> React <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'react'</span>;\n<span class=\"hljs-keyword\">import</span> { storiesOf, action } <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'@kadira/storybook'</span>;\n<span class=\"hljs-keyword\">import</span> Button <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'./Button'</span>;\n<span class=\"hljs-keyword\">import</span> { WithNotes } <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'../notes-addon'</span>;\n\nstoriesOf(<span class=\"hljs-string\">'Button'</span>, <span class=\"hljs-built_in\">module</span>)\n  .add(<span class=\"hljs-string\">'with text'</span>, () =&gt; (\n    <span class=\"xml\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">WithNotes</span> <span class=\"hljs-attr\">notes</span>=<span class=\"hljs-string\">{</span>'<span class=\"hljs-attr\">This</span> <span class=\"hljs-attr\">is</span> <span class=\"hljs-attr\">a</span> <span class=\"hljs-attr\">very</span> <span class=\"hljs-attr\">simple</span> <span class=\"hljs-attr\">Button</span> <span class=\"hljs-attr\">and</span> <span class=\"hljs-attr\">you</span> <span class=\"hljs-attr\">can</span> <span class=\"hljs-attr\">click</span> <span class=\"hljs-attr\">on</span> <span class=\"hljs-attr\">it.</span>'}&gt;</span>\n      <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">Button</span> <span class=\"hljs-attr\">onClick</span>=<span class=\"hljs-string\">{action(</span>'<span class=\"hljs-attr\">clicked</span>')}&gt;</span>Hello Button<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">Button</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">WithNotes</span>&gt;</span></span>\n  ))\n  .add(<span class=\"hljs-string\">'with some emoji'</span>, () =&gt; (\n    <span class=\"xml\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">WithNotes</span> <span class=\"hljs-attr\">notes</span>=<span class=\"hljs-string\">{</span>'<span class=\"hljs-attr\">Here</span> <span class=\"hljs-attr\">we</span> <span class=\"hljs-attr\">use</span> <span class=\"hljs-attr\">some</span> <span class=\"hljs-attr\">emoji</span> <span class=\"hljs-attr\">as</span> <span class=\"hljs-attr\">the</span> <span class=\"hljs-attr\">Button</span> <span class=\"hljs-attr\">text.</span> <span class=\"hljs-attr\">Isn</span>\\'<span class=\"hljs-attr\">t</span> <span class=\"hljs-attr\">it</span> <span class=\"hljs-attr\">look</span> <span class=\"hljs-attr\">nice</span>?'}&gt;</span>\n      <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">Button</span> <span class=\"hljs-attr\">onClick</span>=<span class=\"hljs-string\">{action(</span>'<span class=\"hljs-attr\">clicked</span>')}&gt;</span>😀 😎 👍 💯<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">Button</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">WithNotes</span>&gt;</span></span>\n  ));\n</code></pre>\n<p>Then it will appear in the Notes panel like this:</p>\n<p><img src=\"../static/stories-with-notes.png\" alt=\"With notes\"></p>\n<h2>Setup</h2>\n<p>First, create an <code>addons.js</code> inside the Storybook config directory and add the following content to it.</p>\n<pre><code class=\"language-js\"><span class=\"hljs-comment\">// Storybook's default addons</span>\n<span class=\"hljs-keyword\">import</span> <span class=\"hljs-string\">'@kadira/storybook/addons'</span>;\n</code></pre>\n<p>We’ll use this file shortly to register the Notes addon we are building.</p>\n<p>Now we need to create two files, <code>register.js</code> and <code>index.js,</code> inside a directory called <code>src/notes-addon</code>.</p>\n<h2>The Addon</h2>\n<p>Let’s add the following content to the <code>index.js</code>. It will expose a class called <code>WithNotes</code>, which wraps our story.</p>\n<pre><code class=\"language-js\"><span class=\"hljs-keyword\">import</span> React <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'react'</span>;\n<span class=\"hljs-keyword\">import</span> addons <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'@kadira/storybook-addons'</span>;\n\n<span class=\"hljs-keyword\">export</span> <span class=\"hljs-class\"><span class=\"hljs-keyword\">class</span> <span class=\"hljs-title\">WithNotes</span> <span class=\"hljs-keyword\">extends</span> <span class=\"hljs-title\">React</span>.<span class=\"hljs-title\">Component</span> </span>{\n  render() {\n    <span class=\"hljs-keyword\">const</span> { children, notes } = <span class=\"hljs-keyword\">this</span>.props;\n    <span class=\"hljs-keyword\">const</span> channel = addons.getChannel();\n\n    <span class=\"hljs-comment\">// send the notes to the channel.</span>\n    channel.emit(<span class=\"hljs-string\">'kadira/notes/add_notes'</span>, notes);\n    <span class=\"hljs-comment\">// return children elements.</span>\n    <span class=\"hljs-keyword\">return</span> children;\n  }\n}\n</code></pre>\n<p>In this case, our component can access something called the channel. It lets us communicate with the panel (where we display notes). It has a NodeJS <a href=\"https://nodejs.org/api/events.html\">EventEmitter</a> compatible API.</p>\n<p>In the above case, it will emit the notes text to the channel, so our panel can listen to it.</p>\n<p>Then add the following code to the register.js.</p>\n<p>See: <a href=\"https://gist.github.com/arunoda/fb3859840ff616cc5ea0fa3ef8e3f358\">https://gist.github.com/arunoda/fb3859840ff616cc5ea0fa3ef8e3f358</a></p>\n<p>It will register our addon and add a panel. In this case, the panel represents a React component called <code>Notes</code>. That component has access to the channel and storybook api.</p>\n<p>Then it will listen to the channel and render the notes text on the panel. Have a look at the above annotated code.</p>\n<blockquote>\n<p>In this example, we are only sending messages from the Preview Area to the Manager App (our panel). But we can do it the other way around as well.</p>\n</blockquote>\n<p>It also listens to another event, called onStory, in the storybook API, which fires when the user selects a story. We use that event to clear the previous notes when selecting a story.</p>\n<h3>Register the addon</h3>\n<p>Now, finally, we need to register the addon by importing it to the <code>.storybook/addons.js</code> file.</p>\n<pre><code class=\"language-js\"><span class=\"hljs-comment\">// Storybook's default addons</span>\n<span class=\"hljs-keyword\">import</span> <span class=\"hljs-string\">'@kadira/storybook/addons'</span>;\n\n<span class=\"hljs-comment\">// Our addon</span>\n<span class=\"hljs-keyword\">import</span> <span class=\"hljs-string\">'../src/notes-addon/register'</span>;\n</code></pre>\n<blockquote>\n<p>Above code runs in the Manager App but not in the preview area.</p>\n</blockquote>\n<p>That’s it. Now you can create notes for any story as shown below:</p>\n<pre><code class=\"language-js\"><span class=\"hljs-keyword\">import</span> React <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'react'</span>;\n<span class=\"hljs-keyword\">import</span> { storiesOf, action } <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'@kadira/storybook'</span>;\n<span class=\"hljs-keyword\">import</span> Button <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'./Button'</span>;\n<span class=\"hljs-keyword\">import</span> { WithNotes } <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'../notes-addon'</span>;\n\nstoriesOf(<span class=\"hljs-string\">'Button'</span>, <span class=\"hljs-built_in\">module</span>)\n  .add(<span class=\"hljs-string\">'with text'</span>, () =&gt; (\n    <span class=\"xml\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">WithNotes</span> <span class=\"hljs-attr\">notes</span>=<span class=\"hljs-string\">{</span>'<span class=\"hljs-attr\">This</span> <span class=\"hljs-attr\">is</span> <span class=\"hljs-attr\">a</span> <span class=\"hljs-attr\">very</span> <span class=\"hljs-attr\">simple</span> <span class=\"hljs-attr\">Button</span> <span class=\"hljs-attr\">and</span> <span class=\"hljs-attr\">you</span> <span class=\"hljs-attr\">can</span> <span class=\"hljs-attr\">click</span> <span class=\"hljs-attr\">on</span> <span class=\"hljs-attr\">it.</span>'}&gt;</span>\n      <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">Button</span> <span class=\"hljs-attr\">onClick</span>=<span class=\"hljs-string\">{action(</span>'<span class=\"hljs-attr\">clicked</span>')}&gt;</span>Hello Button<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">Button</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">WithNotes</span>&gt;</span></span>\n  ))\n  .add(<span class=\"hljs-string\">'with some emojies'</span>, () =&gt; (\n    <span class=\"xml\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">WithNotes</span> <span class=\"hljs-attr\">notes</span>=<span class=\"hljs-string\">{</span>'<span class=\"hljs-attr\">Here</span> <span class=\"hljs-attr\">we</span> <span class=\"hljs-attr\">use</span> <span class=\"hljs-attr\">emojies</span> <span class=\"hljs-attr\">as</span> <span class=\"hljs-attr\">the</span> <span class=\"hljs-attr\">Button</span> <span class=\"hljs-attr\">text.</span> <span class=\"hljs-attr\">Isn</span>\\'<span class=\"hljs-attr\">t</span> <span class=\"hljs-attr\">it</span> <span class=\"hljs-attr\">look</span> <span class=\"hljs-attr\">nice</span>?'}&gt;</span>\n      <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">Button</span> <span class=\"hljs-attr\">onClick</span>=<span class=\"hljs-string\">{action(</span>'<span class=\"hljs-attr\">clicked</span>')}&gt;</span>😀 😎 👍 💯<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">Button</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">WithNotes</span>&gt;</span></span>\n  ));\n</code></pre>\n<h2>Addon API</h2>\n<p>Here we’ve only used a few functionalities of our <a href=\"/docs/react-storybook/addons/api\">Addon API</a>.\nYou can learn more about the complete API <a href=\"/docs/react-storybook/addons/api\">here</a>.</p>\n<h2>Packaging</h2>\n<p>You can package this addon into a NPM module very easily. Have a look at this <a href=\"https://github.com/storybooks/storybook/tree/master/packages/addon-notes/tree/version1\">repo</a>.</p>\n<p>In addition to moving the above code to an NPM module, we’ve set <code>react</code> and <code>@kadira/storybook-addons</code> as peer dependencies.</p>\n<h3>Local Development</h3>\n<p>When you are developing your addon as a package, you can’t use <code>npm link</code> to add it your project. Instead add your package as a local dependency into your <code>package.json</code> as shown below:</p>\n<pre><code class=\"language-json\">{\n  ...\n  \"dependencies\": {\n    \"@kadira/storybook-addon-notes\": \"file:///home/username/myrepo\"\n  }\n  ...\n}\n</code></pre>\n<h3>Package Maintenance</h3>\n<p>Your packaged Storybook addon needed to be written in ES5. If you are using ES6, then you need to transpile it.\nIn that case, we recommend to use <a href=\"https://github.com/kadirahq/react-cdk\">React CDK</a> for that.</p>\n"}

