	module.exports = {"id":"exporting-storybook","title":"Exporting Storybook as a Static App","body":"<p>Storybook gives a great developer experience with its dev time features, like instance change updates via Webpack’s HMR.</p>\n<p>But Storybook is also a tool you can use to showcase your components to others.\nDemos of <a href=\"http://necolas.github.io/react-native-web/storybook/\">React Native Web</a> and <a href=\"http://airbnb.io/react-dates/\">React Dates</a> are a good example for that.</p>\n<p>For that, Storybook comes with a tool to export your storybook into a static web app. Then you can deploy it to GitHub pages or any static hosting service.</p>\n<p>Simply add the following NPM script:</p>\n<pre><code class=\"language-sh\">{\n  ...\n  <span class=\"hljs-string\">\"scripts\"</span>: {\n    <span class=\"hljs-string\">\"storybook\"</span>: <span class=\"hljs-string\">\"build-storybook -c .storybook -o .out\"</span>\n  }\n  ...\n}\n</code></pre>\n<p>Then run <code>npm run storybook</code>.</p>\n<p>This will build the storybook configured in the Storybook directory into a static webpack and place it inside the <code>.out</code> directory.\nNow you can deploy the content in the <code>.out</code> directory wherever you want.</p>\n<p>To test it locally, simply run the following commands:</p>\n<pre><code class=\"language-sh\"><span class=\"hljs-built_in\">cd</span> .out\npython -m SimpleHTTPServer\n</code></pre>\n<h2>Deploying to GitHub Pages</h2>\n<p>Additionally, you can deploy Storybook directly into GitHub pages with our <a href=\"https://github.com/storybooks/storybook-deployer\">storybook-deployer</a> tool.</p>\n<p>Or, you can simply export your storybook into the docs directory and use it as the root for GitHub pages. Have a look at <a href=\"https://github.com/blog/2233-publish-your-project-documentation-with-github-pages\">this guide</a> for more information.</p>\n"}

