	module.exports = {"id":"introduction","title":"Introduction","body":"<p>React Storybook is a UI development environment for your React components. With it, you can visualize different states of your UI components and develop them interactively.</p>\n<p>It runs  outside of your app. So you can develop UI components in isolation without worrying about app specific dependencies and requirements.</p>\n<p><img src=\"../static/screenshot.png\" alt=\"React Storybook\"></p>\n<p>React Storybook also comes with a lot of <a href=\"/docs/react-storybook/addons/introduction\">addons</a> and a great API to customize as you wish. You can also build a <a href=\"/docs/react-storybook/basics/exporting-storybook\">static version</a> of your storybook and deploy it anywhere you want.</p>\n<p>Here are some featured storybooks that you can reference to see how Storybook works:</p>\n<ul>\n<li><a href=\"http://kadira-samples.github.io/react-button\">React Button</a> - <a href=\"https://github.com/kadira-samples/react-button\">source</a></li>\n<li><a href=\"http://airbnb.io/react-dates/\">Demo of React Dates</a> - <a href=\"https://github.com/airbnb/react-dates\">source</a></li>\n<li><a href=\"http://necolas.github.io/react-native-web/storybook/\">Demo of React Native Web</a> - <a href=\"https://github.com/necolas/react-native-web\">source</a></li>\n</ul>\n"}

