	module.exports = {"id":"slow-start-guide","title":"Slow Start Guide","body":"<p>You may have tried to use our quick start guide to setup your project for Storybook. If you want to set up Storybook manually, this is the guide for you.</p>\n<blockquote>\n<p>This will also help you to understand how Storybook works.</p>\n</blockquote>\n<h2>Basics</h2>\n<p>Storybook has its own Webpack setup and a dev server. Webpack setup is very similar to <a href=\"https://github.com/facebookincubator/create-react-app\">Create React App</a>, but allows you to configure as you want.</p>\n<p>In this guide, we are trying to set up Storybook for your React project.</p>\n<h2>Add @kadira/storybook</h2>\n<p>First of all, you need to add <code>@kadira/storybook</code> to your project. To do that, simply run:</p>\n<pre><code class=\"language-sh\">npm i --save-dev @kadira/storybook\n</code></pre>\n<h2>Add react and react-dom</h2>\n<p>Make sure that you have <code>react</code> and <code>react-dom</code> in your dependencies as well:</p>\n<pre><code class=\"language-sh\">npm i --save react react-dom\n</code></pre>\n<p>Then add the following NPM script to your package json in order to start the storybook later in this guide:</p>\n<pre><code class=\"language-js\">{\n  ...\n  <span class=\"hljs-string\">\"scripts\"</span>: {\n    <span class=\"hljs-string\">\"storybook\"</span>: <span class=\"hljs-string\">\"start-storybook -p 9001 -c .storybook\"</span>\n  }\n  ...\n}\n</code></pre>\n<h2>Create the config file</h2>\n<p>Storybook can be configured in several different ways. That’s why we need a config directory. We’ve added a <code>-c</code> option to the above NPM script mentioning <code>.storybook</code> as the config directory.</p>\n<p>For the basic Storybook configuration file, you don’t need to do much, but simply tell Storybook where to find stories.</p>\n<p>To do that, simply create a file at <code>.storybook/config.js</code> with the following content:</p>\n<pre><code class=\"language-js\"><span class=\"hljs-keyword\">import</span> { configure } <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'@kadira/storybook'</span>;\n\n<span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> <span class=\"hljs-title\">loadStories</span>(<span class=\"hljs-params\"></span>) </span>{\n  <span class=\"hljs-built_in\">require</span>(<span class=\"hljs-string\">'../stories/index.js'</span>);\n  <span class=\"hljs-comment\">// You can require as many stories as you need.</span>\n}\n\nconfigure(loadStories, <span class=\"hljs-built_in\">module</span>);\n</code></pre>\n<p>That’ll load stories in <code>../stories/index.js</code>.</p>\n<p>Just like that, you can load stories from wherever you want to.</p>\n<h2>Write your stories</h2>\n<p>Now you can write some stories inside the <code>../stories/index.js</code> file, like this:</p>\n<pre><code class=\"language-js\"><span class=\"hljs-keyword\">import</span> React <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'react'</span>;\n<span class=\"hljs-keyword\">import</span> { storiesOf, action } <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'@kadira/storybook'</span>;\n\nstoriesOf(<span class=\"hljs-string\">'Button'</span>, <span class=\"hljs-built_in\">module</span>)\n  .add(<span class=\"hljs-string\">'with text'</span>, () =&gt; (\n    <span class=\"xml\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">button</span> <span class=\"hljs-attr\">onClick</span>=<span class=\"hljs-string\">{action(</span>'<span class=\"hljs-attr\">clicked</span>')}&gt;</span>Hello Button<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">button</span>&gt;</span></span>\n  ))\n  .add(<span class=\"hljs-string\">'with some emoji'</span>, () =&gt; (\n    <span class=\"xml\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">button</span> <span class=\"hljs-attr\">onClick</span>=<span class=\"hljs-string\">{action(</span>'<span class=\"hljs-attr\">clicked</span>')}&gt;</span>😀 😎 👍 💯<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">button</span>&gt;</span></span>\n  ));\n</code></pre>\n<p>Story is a single state of your component. In the above case, there are two stories for the native button component:</p>\n<ol>\n<li>with text</li>\n<li>with some emoji</li>\n</ol>\n<h2>Run your Storybook</h2>\n<p>Now everything is ready. Simply run your storybook with:</p>\n<pre><code class=\"language-js\">npm run storybook\n</code></pre>\n<p>Then you can see all your stories, like this:</p>\n<p><img src=\"../static/basic-stories.png\" alt=\"Basic stories\"></p>\n<p>Now you can change components and write stories whenever you need to. You’ll get those changes into Storybook in a snap with the help of Webpack’s HMR API.</p>\n"}

