	module.exports = {"id":"writing-stories","title":"Writing Stories","body":"<p>Storybook is all about writing stories. Usually a story contains a single state of one of your components. That’s like a visual test case.</p>\n<blockquote>\n<p>Technically, a story is a function that returns a React element.</p>\n</blockquote>\n<p>You can write a set of stories for your components and you’ll get a storybook.</p>\n<h2>Keeping your stories</h2>\n<p>There’s no hard and fast rule for this. But, keeping stories close to your components is a good idea.</p>\n<p>For example, let’s say your UI components live in a directory called: <code>src/components.</code> Then you can write stories inside the <code>src/stories</code> directory.</p>\n<p>This is just one way to do that. You can always edit your storybook config file and ask it to load stories from anywhere you want.</p>\n<h2>Writing stories</h2>\n<p>This is how you write stories:\n(Let’s assume there’s a component called “Button” in <code>src/components/Button.js</code>.)</p>\n<pre><code class=\"language-js\"><span class=\"hljs-comment\">// file: src/stories/index.js</span>\n\n<span class=\"hljs-keyword\">import</span> React <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'react'</span>;\n<span class=\"hljs-keyword\">import</span> { storiesOf, action } <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'@kadira/storybook'</span>;\n<span class=\"hljs-keyword\">import</span> Button <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'../components/Button'</span>;\n\nstoriesOf(<span class=\"hljs-string\">'Button'</span>, <span class=\"hljs-built_in\">module</span>)\n  .add(<span class=\"hljs-string\">'with text'</span>, () =&gt; (\n    <span class=\"xml\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">Button</span> <span class=\"hljs-attr\">onClick</span>=<span class=\"hljs-string\">{action(</span>'<span class=\"hljs-attr\">clicked</span>')}&gt;</span>Hello Button<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">Button</span>&gt;</span></span>\n  ))\n  .add(<span class=\"hljs-string\">'with some emoji'</span>, () =&gt; (\n    <span class=\"xml\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">Button</span> <span class=\"hljs-attr\">onClick</span>=<span class=\"hljs-string\">{action(</span>'<span class=\"hljs-attr\">clicked</span>')}&gt;</span>😀 😎 👍 💯<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">Button</span>&gt;</span></span>\n  ));\n</code></pre>\n<p>This will show stories in your storybook like this:</p>\n<p><img src=\"../static/basic-stories.png\" alt=\"Basic stories\"></p>\n<p>This is just our core API for writing stories. In addition to this, you can use the official and third party Storybook <a href=\"/docs/react-storybook/addons/introduction\">addons</a> to get more functionality.</p>\n<h2>Loading stories dynamically</h2>\n<p>Sometimes, you will want to load your stories dynamically rather than explicitly requiring them in the Storybook config file.</p>\n<p>For example, you may write stories for your app inside the <code>src/components</code> directory with the <code>.stories.js</code> extension. Then you will want to load them at once. Simply edit your config directory at <code>.storybook/config.js</code> as follows:</p>\n<pre><code class=\"language-js\"><span class=\"hljs-keyword\">import</span> { configure } <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'@kadira/storybook'</span>;\n\n<span class=\"hljs-keyword\">const</span> req = <span class=\"hljs-built_in\">require</span>.context(<span class=\"hljs-string\">'../src/components'</span>, <span class=\"hljs-literal\">true</span>, /\\.stories\\.js$/)\n\n<span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> <span class=\"hljs-title\">loadStories</span>(<span class=\"hljs-params\"></span>) </span>{\n  req.keys().forEach(<span class=\"hljs-function\">(<span class=\"hljs-params\">filename</span>) =&gt;</span> req(filename))\n}\n\nconfigure(loadStories, <span class=\"hljs-built_in\">module</span>);\n</code></pre>\n<p>Here we use Webpack’s <a href=\"https://webpack.github.io/docs/context.html#require-context\">require.context</a> to load modules dynamically. Have a look at the relevant Webpack <a href=\"https://webpack.github.io/docs/context.html#require-context\">docs</a> to learn more about how to use require.context.</p>\n<h2>Using Decorators</h2>\n<p>A decorator is a way to wrap a story with a common set of component(s). Let’s say you want to center all your stories. Here is how we can do this with a decorator:</p>\n<pre><code class=\"language-js\"><span class=\"hljs-keyword\">import</span> React <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'react'</span>;\n<span class=\"hljs-keyword\">import</span> { storiesOf } <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'@kadira/storybook'</span>;\n<span class=\"hljs-keyword\">import</span> MyComponent <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'../my_component'</span>;\n\nstoriesOf(<span class=\"hljs-string\">'MyComponent'</span>, <span class=\"hljs-built_in\">module</span>)\n  .addDecorator(<span class=\"hljs-function\">(<span class=\"hljs-params\">story</span>) =&gt;</span> (\n    <span class=\"xml\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span> <span class=\"hljs-attr\">style</span>=<span class=\"hljs-string\">{{textAlign:</span> '<span class=\"hljs-attr\">center</span>'}}&gt;</span>\n      {story()}\n    <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span></span>\n  ))\n  .add(<span class=\"hljs-string\">'without props'</span>, () =&gt; (&lt;MyComponent /&gt;))\n  .add('with some props', () =&gt; (&lt;MyComponent text=\"The Comp\"/&gt;));\n</code></pre>\n<p>Here we only add the decorator for the current set of stories. (In this example, we add it just for the <strong>MyComponent</strong> story group.)</p>\n<p>But, you can also add a decorator <strong>globally</strong> and it’ll be applied to all the stories you create. This is how to add a decorator like that:</p>\n<pre><code class=\"language-js\"><span class=\"hljs-keyword\">import</span> { configure, addDecorator } <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'@kadira/storybook'</span>;\naddDecorator(<span class=\"hljs-function\">(<span class=\"hljs-params\">story</span>) =&gt;</span> (\n  <span class=\"xml\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span> <span class=\"hljs-attr\">style</span>=<span class=\"hljs-string\">{{textAlign:</span> '<span class=\"hljs-attr\">center</span>'}}&gt;</span>\n    {story()}\n  <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span></span>\n));\n\nconfigure(<span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\"></span>) </span>{\n  ...\n}, <span class=\"hljs-built_in\">module</span>);\n</code></pre>\n<h2>Linking stories</h2>\n<p>With <code>linkTo</code> you can link stories together to build demos and prototypes directly from your UI components. (Similar to <a href=\"https://www.invisionapp.com/\">InVision</a> and <a href=\"https://framerjs.com/\">Framer.js</a>)</p>\n<pre><code class=\"language-js\"><span class=\"hljs-keyword\">import</span> { storiesOf, linkTo } <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'@kadira/storybook'</span>\n\nstoriesOf(<span class=\"hljs-string\">'Button'</span>, <span class=\"hljs-built_in\">module</span>)\n  .add(<span class=\"hljs-string\">'First'</span>, () =&gt; (\n    <span class=\"xml\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">button</span> <span class=\"hljs-attr\">onClick</span>=<span class=\"hljs-string\">{linkTo(</span>'<span class=\"hljs-attr\">Button</span>', '<span class=\"hljs-attr\">Second</span>')}&gt;</span>Go to \"Second\"<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">button</span>&gt;</span></span>\n  ))\n  .add(<span class=\"hljs-string\">'Second'</span>, () =&gt; (\n    <span class=\"xml\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">button</span> <span class=\"hljs-attr\">onClick</span>=<span class=\"hljs-string\">{linkTo(</span>'<span class=\"hljs-attr\">Button</span>', '<span class=\"hljs-attr\">First</span>')}&gt;</span>Go to \"First\"<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">button</span>&gt;</span></span>\n  ));\n</code></pre>\n<p>With that, you can link an event in a component to any story in the Storybook.</p>\n<ul>\n<li>First parameter is the story kind (what you named with storiesOf).</li>\n<li>Second parameter is the story name (what you named with .add).</li>\n</ul>\n<p>You can also pass a function instead for any of above parameters. That function accepts arguments emitted by the event and it should return a string. For example:</p>\n<pre><code class=\"language-js\">linkTo(<span class=\"hljs-function\"><span class=\"hljs-params\">()</span> =&gt;</span> <span class=\"hljs-string\">'Button'</span>, () =&gt; <span class=\"hljs-string\">'Second'</span>)\n</code></pre>\n<h2>Managing stories</h2>\n<p>Storybook has a very simple API to write stories. With that, you can’t display nested stories.\n<em><strong>This is something we’ve done purposely.</strong></em></p>\n<p>But you might ask, how do I manage stories If I have many of them? Here’s how different developers address this issue. Therefore, there’s no need to build a built-in feature for this (at least in the short term).</p>\n<h3>Prefix with dots</h3>\n<p>For example, you can prefix story names with a dot (<code>.</code>):</p>\n<pre><code class=\"language-js\">storiesOf(<span class=\"hljs-string\">'core.Button'</span>, <span class=\"hljs-built_in\">module</span>)\n</code></pre>\n<p>Then you can filter stories to display only the stories you want to see.</p>\n<h3>Run multiple storybooks</h3>\n<p>You can run multiple storybooks for different kinds of stories (or components). To do that, you can create different NPM scripts to start different stories. See:</p>\n<pre><code class=\"language-js\">{\n   ...\n   <span class=\"hljs-string\">\"scripts\"</span>: {\n     <span class=\"hljs-string\">\"start-storybook-for-theme\"</span>: <span class=\"hljs-string\">\"start-storybook -p 9001 -c .storybook-theme\"</span>\n     <span class=\"hljs-string\">\"start-storybook-for-app\"</span>: <span class=\"hljs-string\">\"start-storybook -p 8001 -c .storybook-app\"</span>\n   }\n   ...\n}\n</code></pre>\n<h3>Use multiple repos</h3>\n<p>This is a popular option. You can create different repos for different kinds of UI components and have a storybook for each of them. Here are some ways to separate them:</p>\n<ul>\n<li>Have one repo for the theme, and one for the app.</li>\n<li>Have one repo for each UI component and use those in different apps.</li>\n<li>Have a few repos for different kinds of UI components and use them in different apps.</li>\n</ul>\n"}

