	module.exports = {"id":"add-custom-head-tags","title":"Add Custom Head Tags","body":"<p>Sometimes, you may need to add different tags to the HTML head. This is useful for adding web fonts or some external scripts.</p>\n<p>You can do this very easily. Simply create a file called <code>head.html</code> inside the Storybook config directory and add tags like this:</p>\n<pre><code class=\"language-html\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">script</span> <span class=\"hljs-attr\">src</span>=<span class=\"hljs-string\">\"https://use.typekit.net/xxxyyy.js\"</span>&gt;</span><span class=\"undefined\"></span><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">script</span>&gt;</span>\n<span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">script</span>&gt;</span><span class=\"actionscript\"><span class=\"hljs-keyword\">try</span>{ Typekit.load(); } <span class=\"hljs-keyword\">catch</span>(e){ }</span><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">script</span>&gt;</span>\n</code></pre>\n<p>That’s it. Storybook will inject these tags.</p>\n<blockquote>\n<p><strong>Important</strong></p>\n</blockquote>\n<blockquote>\n<p>Storybook will inject these tags to the iframe where your components are rendered. So, these won’t be loaded into the main Storybook UI.</p>\n</blockquote>\n"}

