	module.exports = {"id":"cli-options","title":"CLI Options","body":"<p>React Storybook comes with two CLI utilities. They are <code>start-storybook</code> and <code>build-storybook</code>.</p>\n<p>They have some options you can pass to alter the storybook behaviors. We have seen some of them in previous docs.</p>\n<p>Here are all those options:</p>\n<h2>For start-storybook</h2>\n<pre><code><span class=\"hljs-attribute\">Usage</span>: start-storybook [options]\n\n<span class=\"vim\">Option<span class=\"hljs-variable\">s:</span>\n\n  -h, --<span class=\"hljs-keyword\">help</span>                    output usage information\n  -V, --<span class=\"hljs-keyword\">version</span>                 output the <span class=\"hljs-keyword\">version</span> <span class=\"hljs-keyword\">number</span>\n  -<span class=\"hljs-keyword\">p</span>, --port [<span class=\"hljs-keyword\">number</span>]           Port <span class=\"hljs-keyword\">to</span> run Storybook (Required)\n  -h, --host [<span class=\"hljs-built_in\">string</span>]           Host <span class=\"hljs-keyword\">to</span> run Storybook\n  -s, --static-dir <span class=\"hljs-symbol\">&lt;dir-names&gt;</span>  Directory where <span class=\"hljs-keyword\">to</span> load static <span class=\"hljs-keyword\">files</span> from\n  -<span class=\"hljs-keyword\">c</span>, --config-dir [dir-name]   Directory where <span class=\"hljs-keyword\">to</span> load Storybook configurations from\n</span></code></pre>\n<h2>For build-storybook</h2>\n<pre><code><span class=\"hljs-attribute\">Usage</span>: build-storybook [options]\n\n<span class=\"mipsasm\"><span class=\"hljs-symbol\">Options:</span>\n\n  -h, --help                    output usage information\n  -V, --version                 output the version number\n  -s, --static-<span class=\"hljs-keyword\">dir </span>&lt;<span class=\"hljs-keyword\">dir-names&gt; </span> <span class=\"hljs-keyword\">Directory </span>where to load static files from\n  -o, --output-<span class=\"hljs-keyword\">dir </span>[<span class=\"hljs-keyword\">dir-name] </span>  <span class=\"hljs-keyword\">Directory </span>where to store <span class=\"hljs-keyword\">built </span>files\n  -c, --<span class=\"hljs-built_in\">config</span>-<span class=\"hljs-keyword\">dir </span>[<span class=\"hljs-keyword\">dir-name] </span>  <span class=\"hljs-keyword\">Directory </span>where to load Storybook configurations from\n</span></code></pre>\n"}

