	module.exports = {"id":"custom-webpack-config","title":"Custom Webpack Config","body":"<p>The default Webpack config of React Storybook is usually well balanced for a medium-size React project (specially created with <a href=\"https://github.com/facebookincubator/create-react-app\">Create React App</a>) or a library. But if you already have your own Webpack setup, that’s not useable.</p>\n<p>That’s why we allow you to customize our Webpack setup. There are a few ways to do it. Let’s discuss:</p>\n<h2>Simple Mode</h2>\n<p>Let’s say you want to add <a href=\"http://sass-lang.com/\">SASS</a> support to Storybook. This is how to do it.\nSimply add the following content to a file called <code>webpack.config.js</code> in your Storybook config directory (<code>.storybook</code> by default ).</p>\n<pre><code class=\"language-js\"><span class=\"hljs-keyword\">const</span> path = <span class=\"hljs-built_in\">require</span>(<span class=\"hljs-string\">'path'</span>);\n\n<span class=\"hljs-built_in\">module</span>.exports = {\n  <span class=\"hljs-attr\">module</span>: {\n    <span class=\"hljs-attr\">loaders</span>: [\n      {\n        <span class=\"hljs-attr\">test</span>: <span class=\"hljs-regexp\">/\\.scss$/</span>,\n        <span class=\"hljs-attr\">loaders</span>: [<span class=\"hljs-string\">\"style\"</span>, <span class=\"hljs-string\">\"css\"</span>, <span class=\"hljs-string\">\"sass\"</span>],\n        <span class=\"hljs-attr\">include</span>: path.resolve(__dirname, <span class=\"hljs-string\">'../'</span>)\n      }\n    ]\n  }\n}\n</code></pre>\n<p>Since this config file stays in the Storybook directory, you need to set the include path as above. If the config directory stays in a different directory, you need to set the include path relative to that.</p>\n<p>You also need to install the loaders (style, css, and sass) used in above config manually.</p>\n<blockquote>\n<p>Once you create this <code>webpack.config.js</code> file, Storybook won’t load the <a href=\"/docs/react-storybook/configurations/default-config\">default Webpack config</a> other than loading JS files with the Babel loader.</p>\n</blockquote>\n<h3>Supported Webpack Options</h3>\n<p>You can add any kind of Webpack configuration options with the above config, whether they are plugins, loaders, or aliases.\nBut you won’t be able to change the following config options:</p>\n<ul>\n<li>entry</li>\n<li>output</li>\n<li>js loader with babel</li>\n</ul>\n<h2>Full Control Mode</h2>\n<p>Sometimes, you will want to have full control over the webpack configuration. Maybe you want to add different configurations for dev and production environments. That’s where you can use our full control mode.</p>\n<p>To enable that, you need to export a <strong>function</strong> from the above <code>webpack.config.js</code> file, just like this:</p>\n<pre><code class=\"language-js\"><span class=\"hljs-comment\">// Export a function. Accept the base config as the only param.</span>\n<span class=\"hljs-built_in\">module</span>.exports = <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span>(<span class=\"hljs-params\">storybookBaseConfig, configType</span>) </span>{\n  <span class=\"hljs-comment\">// configType has a value of 'DEVELOPMENT' or 'PRODUCTION'</span>\n  <span class=\"hljs-comment\">// You can change the configuration based on that.</span>\n  <span class=\"hljs-comment\">// 'PRODUCTION' is used when building the static version of storybook.</span>\n\n  <span class=\"hljs-comment\">// Make whatever fine-grained changes you need</span>\n  storybookBaseConfig.module.loaders.push({\n    <span class=\"hljs-attr\">test</span>: <span class=\"hljs-regexp\">/\\.scss$/</span>,\n    <span class=\"hljs-attr\">loaders</span>: [<span class=\"hljs-string\">\"style\"</span>, <span class=\"hljs-string\">\"css\"</span>, <span class=\"hljs-string\">\"sass\"</span>],\n    <span class=\"hljs-attr\">include</span>: path.resolve(__dirname, <span class=\"hljs-string\">'../'</span>)\n  });\n\n  <span class=\"hljs-comment\">// Return the altered config</span>\n  <span class=\"hljs-keyword\">return</span> storybookBaseConfig;\n};\n</code></pre>\n<p>Storybook uses the config returned from the above function. So, try to edit the <code>storybookBaseConfig</code> with care. Make sure to preserve the following config options:</p>\n<ul>\n<li>entry</li>\n<li>output</li>\n<li>first loader in the module.loaders (Babel loader for JS)</li>\n</ul>\n<p>Other than that, you should try to keep the default set of plugins.</p>\n<h2>Extending The Default Config</h2>\n<p>You may want to keep Storybook’s <a href=\"/docs/react-storybook/configurations/default-config\">default config</a>, but just need to extend it. If so, this is how you do it using the Full Control Mode.\nAdd following content to the <code>webpack.config.js</code> in your Storybook config directory.</p>\n<pre><code class=\"language-js\"><span class=\"hljs-comment\">// load the default config generator.</span>\n<span class=\"hljs-keyword\">var</span> genDefaultConfig = <span class=\"hljs-built_in\">require</span>(<span class=\"hljs-string\">'@kadira/storybook/dist/server/config/defaults/webpack.config.js'</span>);\n\n<span class=\"hljs-built_in\">module</span>.exports = <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span>(<span class=\"hljs-params\">config, env</span>) </span>{\n  <span class=\"hljs-keyword\">var</span> config = genDefaultConfig(config, env);\n\n  <span class=\"hljs-comment\">// Extend it as you need.</span>\n\n  <span class=\"hljs-keyword\">return</span> config;\n};\n</code></pre>\n<h2>Using Your Existing Config</h2>\n<p>You may have an existing Webpack config for your project. So, you may need to copy and paste some config items into Storybook’s custom Webpack config file.</p>\n<p>But you don’t need to. There are a few options:</p>\n<ul>\n<li>Simply import your main Webpack config into Storybook’s <code>webpack.config.js</code> and use the loaders and plugins used in that.</li>\n<li>Create a new file with common Webpack options and use it in both inside the main Webpack config and inside Storybook’s <code>webpack.config.js</code>.</li>\n</ul>\n"}

