	module.exports = {"id":"default-config","title":"Default Config","body":"<p>Storybook has a default Webpack setup which is similar to <a href=\"https://github.com/facebookincubator/create-react-app\">Create React App</a>.\nLet’s learn about the default config comes with Storybook.</p>\n<h2>Babel</h2>\n<p>We use Babel for JavaScript(ES6) transpiling. Here are some key features of Storybook’s Babel configurations.</p>\n<h3>ES2016+ Support</h3>\n<p>We have added ES2016 support with Babel for transpiling your JS code. In addition to that, we’ve added a few experimental features, like object spreading and async await. Check out our <a href=\"https://github.com/storybooks/storybook/blob/master/packages/react-storybook/src/server/config/babel.js#L19\">source</a> to learn more about these plugins.</p>\n<h3>.babelrc support</h3>\n<p>If your project has a <code>.babelrc</code> file, we’ll use that instead of the default config file. So, you could use any babel plugins or presets that you have used in your project with Storybook.</p>\n<h2>Webpack</h2>\n<p>We use Webpack to serve and load JavaScript modules for the web. We’ve added some Webpack loaders to bring some good defaults. (This setup is very close to what you get with the <a href=\"https://github.com/facebookincubator/create-react-app\">Create React App</a>.)</p>\n<h3>CSS Support</h3>\n<p>You can simply import CSS files wherever you want, whether it’s in the storybook config file, a UI component, or inside a story definition file.</p>\n<p>Basically, you can import CSS like this:</p>\n<pre><code class=\"language-js\"><span class=\"hljs-comment\">// locally</span>\n<span class=\"hljs-keyword\">import</span> <span class=\"hljs-string\">'./styles.css'</span>\n<span class=\"hljs-comment\">// or from NPM modules</span>\n<span class=\"hljs-keyword\">import</span> <span class=\"hljs-string\">'bootstrap/dist/css/bootstrap.css'</span>;\n</code></pre>\n<h3>Image and Static File Support</h3>\n<p>You can also import images and media files directly via JavaScript. This helps you to write stories with media files easily. This is how to do it:</p>\n<pre><code class=\"language-js\"><span class=\"hljs-keyword\">import</span> React <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'react'</span>;\n<span class=\"hljs-keyword\">import</span> { storiesOf, action } <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'@kadira/storybook'</span>;\n\n<span class=\"hljs-keyword\">import</span> imageFile <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'./static/image.png'</span>;\n\nstoriesOf(<span class=\"hljs-string\">'&lt;img&gt;'</span>, <span class=\"hljs-built_in\">module</span>)\n  .add(<span class=\"hljs-string\">'with a image'</span>, () =&gt; (\n    <span class=\"xml\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">img</span> <span class=\"hljs-attr\">src</span>=<span class=\"hljs-string\">{imageFile}</span> /&gt;</span>\n  ));\n</span></code></pre>\n<p>When you are building a storybook, we’ll also export the imported image. So, this is a good approach to loading all of your static content.</p>\n<blockquote>\n<p>Storybook also has a way to mention static directories via the -s option of the <code>react-storybook</code> and <code>build-storybook</code> commands.</p>\n</blockquote>\n<h3>JSON Loader</h3>\n<p>You can import <code>.json</code> files, as you do with Node.js. This will also allow you to use NPM projects, which imports <code>.json</code> files inside them.</p>\n<h2>NPM Modules</h2>\n<p>You can use any of the NPM modules installed on your project. You can simply import and use them.</p>\n<blockquote>\n<p>Unfortunately, we don’t support Meteor packages. If your UI component includes one or more Meteor packages, try to avoid using them in UI components.\nIf they are containers, you can use <a href=\"https://github.com/kadirahq/react-stubber\">React Stubber</a> to use them in Storybook.</p>\n</blockquote>\n"}

