	module.exports = {"body":"<hr>\n<h2>id: 'env-vars’\ntitle: ‘Using Environment Variables’</h2>\n<p>Sometimes, we may use configuration items inside Storybook. It might be a theme color, some client secret, or a JSON string. So, we usually tend to hard code them.</p>\n<p>But you can expose those configurations via “environment variables.” For that, you need to prefix your environment variables with <code>STORYBOOK_</code> prefix.</p>\n<p>For an example, let’s expose two environment variables like this:</p>\n<pre><code class=\"language-sh\">STORYBOOK_THEME=red STORYBOOK_DATA_KEY=12345 npm run storybook\n</code></pre>\n<p>Then we can access these environment variables anywhere inside our JS code like below:</p>\n<pre><code class=\"language-js\"><span class=\"hljs-built_in\">console</span>.log(process.env.STORYBOOK_THEME)\n<span class=\"hljs-built_in\">console</span>.log(process.env.STORYBOOK_DATA_KEY)\n</code></pre>\n<blockquote>\n<p>Even though we can access these env variables anywhere in the client side JS code, it’s better to use them only inside stories and inside the main Storybook config file.</p>\n</blockquote>\n<h2>Build time environment variables</h2>\n<p>You can also pass these environment variables when you are <a href=\"/docs/react-storybook/basics/exporting-storybook\">building your storybook</a> with <code>build-storybook</code>.</p>\n<p>Then they’ll be hard coded to the static version of your Storybook.</p>\n<h2>NODE_ENV env variable</h2>\n<p>In addition to the above prefixed environment variables, you can also pass the NODE_ENV variable to Storybook. But, you normally don’t need to do that since we set a reasonable default value for it.</p>\n<ul>\n<li>When running <code>npm run storybook</code>, we set NODE_ENV to ‘development’</li>\n<li>When building storybook, we set NODE_ENV to ‘production’</li>\n</ul>\n"}

