	module.exports = {"id":"serving-static-files","title":"Serving Static Files","body":"<p>It’s often useful to load static files like images and videos when creating components and stories.</p>\n<p>Storybook provides two ways to do that.</p>\n<h2>1. Via Imports</h2>\n<p>You can import any media assets by simply importing (or requiring) them as shown below.</p>\n<pre><code class=\"language-js\"><span class=\"hljs-keyword\">import</span> React <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'react'</span>;\n<span class=\"hljs-keyword\">import</span> { storiesOf, action } <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'@kadira/storybook'</span>;\n\n<span class=\"hljs-keyword\">import</span> imageFile <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'./static/image.png'</span>;\n\nstoriesOf(<span class=\"hljs-string\">'&lt;img&gt;'</span>, <span class=\"hljs-built_in\">module</span>)\n  .add(<span class=\"hljs-string\">'with a image'</span>, () =&gt; (\n    <span class=\"xml\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">img</span> <span class=\"hljs-attr\">src</span>=<span class=\"hljs-string\">{imageFile}</span> /&gt;</span>\n  ));\n</span></code></pre>\n<p>This is enabled with our <a href=\"/docs/react-storybook/configurations/default-config\">default config</a>. But, if you are using a <a href=\"/docs/react-storybook/configurations/custom-webpack-config\">custom Webpack config</a>, you need to add the <a href=\"https://github.com/webpack/file-loader\">file-loader</a> into your custom Webpack config.</p>\n<h2>2. Via a Directory</h2>\n<p>You can also configure a directory (or a list of directories) for searching static content when you are starting Storybook. You can do that with the -s flag.</p>\n<p>See the following npm script on how to use it:</p>\n<pre><code class=\"language-js\">{\n  <span class=\"hljs-string\">\"scripts\"</span>: {\n    <span class=\"hljs-string\">\"start-storybook\"</span>: <span class=\"hljs-string\">\"start-storybook -s ./public -p 9001\"</span>\n  }\n}\n</code></pre>\n<p>Here <code>./public</code> is our static directory. Now you can use static files in the public directory in your components or stories like this.</p>\n<pre><code class=\"language-js\"><span class=\"hljs-keyword\">import</span> React <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'react'</span>;\n<span class=\"hljs-keyword\">import</span> { storiesOf, action } <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'@kadira/storybook'</span>;\n\n<span class=\"hljs-comment\">// assume image.png is located in the \"public\" directory.</span>\nstoriesOf(<span class=\"hljs-string\">'&lt;img&gt;'</span>, <span class=\"hljs-built_in\">module</span>)\n  .add(<span class=\"hljs-string\">'with a image'</span>, () =&gt; (\n    <span class=\"xml\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">img</span> <span class=\"hljs-attr\">src</span>=<span class=\"hljs-string\">\"/image.png\"</span> /&gt;</span>\n  ));\n</span></code></pre>\n<blockquote>\n<p>You can also pass a list of directories, instead of a single directory, as shown below.</p>\n<pre><code class=\"language-js\">{\n  <span class=\"hljs-string\">\"scripts\"</span>: {\n    <span class=\"hljs-string\">\"start-storybook\"</span>: <span class=\"hljs-string\">\"start-storybook -s ./public,./static -p 9001\"</span>\n  }\n}\n</code></pre>\n</blockquote>\n<h2>Absolute versus relative paths</h2>\n<p>Sometimes, you may want to deploy your storybook into a subpath, like <a href=\"https://kadira-samples.github.io/react-button\">https://kadira-samples.github.io/react-button</a>.</p>\n<p>In this case, you need to have all your images and media files with relative paths. Otherwise, Storybook cannot locate those files.</p>\n<p>If you load static content via importing, this is automatic and you do not have to do anything.</p>\n<p>If you are using a static directory, then you need to use <em>relative paths</em> to load images.</p>\n"}

