	module.exports = {"id":"css-style-testing","title":"CSS/Style Testing","body":"<p>We can also use Storybook as the base for CSS/Style testing with stories as the base. First, have a look at the following Storybook.</p>\n<p><img src=\"../static/storybook-screenshot.png\" alt=\"Storybook Screenshot\"></p>\n<p>In that, you can see the Storybook’s manager UI. It has UI elements that are not related to your app. However, there’s a way to access just a single story.</p>\n<p>For an example, let’s assume the above storybook runs on port 9009 and we can access it via <a href=\"http://localhost:9009/\">http://localhost:9009</a>.\nThen Let’s pick a single story: the “with text” story of the Button. So, in this case:</p>\n<ul>\n<li>selectedKind = Button</li>\n<li>selectedStory = with text</li>\n</ul>\n<p>Then, we can see the above story using the following URL:</p>\n<p><a href=\"http://localhost:9009/iframe.html?selectedKind=Button&amp;selectedStory=with+text&amp;dataId=0\">http://localhost:9009/iframe.html?selectedKind=Button&amp;selectedStory=with+text&amp;dataId=0</a></p>\n<p><img src=\"../static/storybook-iframe-screenshot.png\" alt=\"Storybook Iframe Screenshot\"></p>\n<p>Just like that, you can access all of the stories in your Storybook.</p>\n<h2>Supported CSS/Style Testing Frameworks</h2>\n<p>It will be hard to use all the frameworks we’ve <a href=\"/docs/react-storybook/testing/react-ui-testing#3-css-style-testing\">mentioned</a>, but we’ll be able to use frameworks which are based on URL as the input source. (Such as <a href=\"https://github.com/garris/BackstopJS\">BackstopJS</a> and <a href=\"https://github.com/gemini-testing/gemini\">Gemini</a>)</p>\n<blockquote>\n<p>In the future we are also planning to smooth this process with the help of <a href=\"https://github.com/storybooks/storyshots\">StoryShots</a>.</p>\n</blockquote>\n"}

