	module.exports = {"id":"interaction-testing","title":"Interaction Testing","body":"<p>For the interaction testing, <a href=\"https://github.com/airbnb/enzyme\">Enzyme</a> is the best tool we can use. With that, we can <a href=\"http://airbnb.io/enzyme/docs/api/ReactWrapper/simulate.html\">simulate</a> user inputs and see what they are doing.</p>\n<p>You can directly write these kind of tests with a full-featured testing framework, such as <strong>Mocha</strong> or <strong>Jest</strong>. Have a look at the <a href=\"https://github.com/airbnb/enzyme/\">Enzyme guidelines</a> for more information on how to integrate them.</p>\n<h2>Specs Addon</h2>\n<p>If you like to write your tests directly inside your stories, we also have an addon called <a href=\"https://github.com/mthuret/storybook-addon-specifications\">specs</a>.</p>\n<p><img src=\"../static/specs-addon.png\" alt=\"Storybook Specs Addon\"></p>\n<p>With that, you can write test specs directly inside stories.\nAdditionally, you also can use your CI server to run those tests.</p>\n"}

