	module.exports = {"body":"<hr>\n<h2>id: 'manual-testing’\ntitle: ‘Manual Testing’</h2>\n<p>Now we arrive at the most interesting (but also hardest) part of UI testing. We usually do this as a team.</p>\n<p>First, we need to make a pretty solid Storybook or a set of Storybooks covering most of the scenarios of our UI components. For that we can follow the following structure:</p>\n<ul>\n<li>Write stories for your individual UI components.</li>\n<li>Write another set of stories for integrating the above UI components (you could consider your pages).</li>\n</ul>\n<p>For the individual UI components, you may be using a different repository. Then, keep a storybook inside it for those components. Then, in the main app, write stories for integrations.</p>\n<h2>Testing Plan</h2>\n<p>Open a new PR (or multiple of them). Then run your Storybook and start reviewing one story at a time. Then you can comment on the PR.</p>\n<blockquote>\n<p>Join our <a href=\"http://tinyletter.com/storybooks\">Newsletter</a> or <a href=\"https://storybooks-slackin.herokuapp.com/\">Slack Team</a> to get updates.</p>\n</blockquote>\n"}

