	module.exports = {"id":"react-ui-testing","title":"Introduction: React UI Testing","body":"<p>There are different aspects we need to look at when testing UI. There are also a lot of tools and techniques we can use.</p>\n<h2>Reasons for Testing</h2>\n<p>Before we talk about testing, we need to think about why we need to test. There are many reasons; here are some of our reasons:</p>\n<ul>\n<li>To find bugs.</li>\n<li>To make sure things won’t break between new code commits.</li>\n<li>To keep tests as living documentations.</li>\n</ul>\n<p>Specifically, testing is important when working with teams since it allows different people the ability to contribute with confidence.</p>\n<h2>Different Aspects of UI Testing</h2>\n<p>We refer UI for many things. To put this in focus, let’s narrow it down to React based user interfaces.</p>\n<h3>1. Structural Testing</h3>\n<p>Here we’ll focus on the structure of the UI and how it’s laid out. For an example, let’s say we have a “login component” as shown below:</p>\n<p><img src=\"../static/login_form.png\" alt=\"Login Form\"></p>\n<p>For structural testing, we are testing whether or not it has following content:</p>\n<ul>\n<li>A title with “Login in to Facebook”</li>\n<li>Two inputs for the username and password.</li>\n<li>A submit button.</li>\n<li>An error screen to show errors.</li>\n</ul>\n<p>For React, we have been using <a href=\"https://github.com/airbnb/enzyme\">Enzyme</a> as a way to do structural testing, but now we can also use <a href=\"https://facebook.github.io/jest/blog/2016/07/27/jest-14.html\">Jest’s snapshot testing</a> to make things even more simple.</p>\n<h3>2. Interaction Testing</h3>\n<p>UI is all about interacting with the user. We do this with a bunch of UI elements, such as buttons, links, and input elements. With interaction testing, we need to test if they are working properly.</p>\n<p>Let’s again use the above login component as an example. It should do these things:</p>\n<ul>\n<li>When we click the submit button, it should give us the username and password.</li>\n<li>When we click the “Forgotten Account” link, it should redirect to a new page.</li>\n</ul>\n<p>We have few ways to do this type of testing with React. The simple way is to use <a href=\"https://github.com/airbnb/enzyme\">Enzyme</a>.</p>\n<h3>3. CSS/Style Testing</h3>\n<p>UI is all about styles (whether they’re simple, beautiful, or even ugly). With style testing, we are evaluating the look and feel of our UI components between code changes. This is a pretty complex subject and usually we do it by comparing images.</p>\n<p>If we are using inline styles all the way, we can use JEST snapshot testing. But to get even better results, we should consider using tools such as:</p>\n<ul>\n<li><a href=\"https://github.com/garris/BackstopJS\">BackstopJS</a></li>\n<li><a href=\"https://github.com/Huddle/PhantomCSS\">PhantomCSS</a></li>\n<li><a href=\"https://github.com/gemini-testing/gemini\">Gemini</a></li>\n<li><a href=\"https://github.com/Galooshi/happo\">Happo</a></li>\n</ul>\n<h3>4. Manual Testing</h3>\n<p>All the above sections are about testing with automated tools. But since we are building UI for humans, we must also manually test them to see how they feel.</p>\n<p>Another reason for manual testing is for the better user experience.</p>\n<p>We should always try to test our UI with the naked eye. For this, we can simply use our existing Storybook.This is something that we can’t automate(yet) and takes time. But it would be great if we could do this once in a while (especially with a major code changes).</p>\n<h2>How Storybook Can Help Us</h2>\n<p>A <strong>story</strong> is a smallest unit in Storybook. It’s a fully functioning UI element where the input can be used for any of the testing methods we’ve mentioned above.</p>\n<p>Let’s look at how Storybook can help you do the above mentioned different aspects of testing.</p>\n<ul>\n<li><a href=\"/docs/react-storybook/testing/structural-testing\">Structural Testing with StoryShots</a></li>\n<li><a href=\"/docs/react-storybook/testing/interaction-testing\">Interaction Testing with Specs Addon</a></li>\n<li><a href=\"/docs/react-storybook/testing/css-style-testing\">Storybook as the Base for CSS/Style Testing</a></li>\n<li><a href=\"/docs/react-storybook/testing/manual-testing\">Storybook for Manual UI Testing</a></li>\n</ul>\n"}

